import React from "react";
import {
  Typography,
  Box,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Button,
} from "@mui/material";
import { Download } from "@material-ui/icons";

const Prescriptions = [
  {
    id: "1",
    name: "Examen 1",
    summaryExam: "Lorend resumen ",
    doctorname: "Elite Admin",
    priority: "Download",
    pbg: "primary.main",
    examDate: "04/06/2022",
  },
  {
    id: "2",
    name: "Examen 2",
    summaryExam: "10/05/2022",
    doctorname: "Andrew McDownland",
    priority: "Download",
    pbg: "primary.main",
    examDate: "14/05/2022",
  },
  {
    id: "3",
    name: "Examen 3",
    summaryExam: "15/07/2022",
    doctorname: "Christopher Jamil",
    priority: "Download",
    pbg: "primary.main",
    examDate: "11/05/2022",
  },
  {
    id: "4",
    name: "Examen 4",
    summaryExam: "25/09/2022",
    doctorname: "Christopher JamilL",
    priority: "Download",
    pbg: "primary.main",
    examDate: "15/05/2022",
  },
];

const ExTableExamenes = () => {
  return (
    <Table
      aria-label="simple table"
      sx={{
        mt: 3,
        whiteSpace: "nowrap",
      }}
    >
      <TableHead>
        <TableRow>
          <TableCell>
            <Typography color="textSecondary" variant="h6">
              Id
            </Typography>
          </TableCell>
          <TableCell>
            <Typography color="textSecondary" variant="h6">
              Name Exams
            </Typography>
          </TableCell>
          <TableCell>
            <Typography color="textSecondary" variant="h6">
              Name Doctor
            </Typography>
          </TableCell>
          <TableCell>
            <Typography color="textSecondary" variant="h6">
              Date
            </Typography>
          </TableCell>
          <TableCell align="right">
            <Typography color="textSecondary" variant="h6" paddingRight={2}>
              Download
            </Typography>
          </TableCell>

        </TableRow>
      </TableHead>
      <TableBody>
        {Prescriptions.map((Prescriptions) => (
          <TableRow key={Prescriptions.id}>
            <TableCell>
              <Typography
                sx={{
                  fontSize: "15px",
                  fontWeight: "500",
                }}
              >
                {Prescriptions.id}
              </Typography>
            </TableCell>
            <TableCell>
              <Box
                sx={{
                  display: "flex",
                  alignItems: "center",
                }}
              >
                <Box>
                  <Typography
                    variant="h6"
                    sx={{
                      fontWeight: "600",
                    }}
                  >
                    {Prescriptions.name}
                  </Typography>
                  <Typography
                    color="textSecondary"
                    sx={{
                      fontSize: "13px",
                    }}
                  >
                    {Prescriptions.summaryExam}
                  </Typography>
                </Box>
              </Box>
            </TableCell>
            <TableCell>
              <Typography color="textSecondary" variant="h6">
                {Prescriptions.doctorname}
              </Typography>
            </TableCell>
            <TableCell>
              <Typography color="textSecondary" variant="h6">
                {Prescriptions.examDate}
              </Typography>
            </TableCell>
            <TableCell align="right">
              <Button 
                startIcon={<Download/> } title="Descargar" variant="contained" 
                sx={{ 
                
                  backgroundColor: Prescriptions.pbg,
                  color:"#FFFFFF" 
                  }}
                size="small"
                >
                  {Prescriptions.priority}
              </Button>
            </TableCell>
           
          </TableRow>
        ))}
      </TableBody>
    </Table>
  );
};

export default ExTableExamenes;
